export default {
	name: 'goodsItem',
	props: {
		obj: {
			type: Object
		},
		width: {
			type: String,
			value: 179
		}
	},
	created() {
		
	},
	methods:{
		goodsClick(obj){
			this.$emit('goodsClick',obj.goods_id)
		},
		carClick(obj){
			this.$emit('carClick',obj.goods_id)
		}
	}
}
